import React, { useState, useEffect } from 'react';
import { useLocation } from 'umi';
import { BorderOuterOutlined, CloudOutlined, ControlOutlined, ExceptionOutlined } from '@ant-design/icons';
import { Row, Col, Card, Slider, Steps, Skeleton, Spin } from 'antd';
import styles from './index.less';
import MainGraph from './Graph/main.js';
import Overview from './overview.js';


// faultState: 1 是恢复，0 是故障； 关闭窗口 将faultState清理掉
const Fault = () => {

  const location = useLocation();
  // let initf = location.search.split('?')[1].split("=")[1];

  const [currentTime, setCurrentTime] = useState(new Date());

  useEffect(() => {
    // 创建一个新的定时器，在每一秒钟执行一次函数来更新时间状态。
    const timerId = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);

    // 清除定时器以防止内存泄漏。
    return () => clearInterval(timerId);
  }, []);

  // 定时5秒后，修改状态
  const [restore, setRestore] = useState(false);
  // 点击accept以后，将状态调整成true
  const getFaultState = () => {
    // 使用 setTimeout 实现3秒延迟
    setTimeout(() => {
      setRestore(true) // 执行实际的操作
    }, 1000); // 2000毫秒等于3秒
  }

  const [currentStep, setCurrentStep] = useState(0);

  let ss = 0;

  useEffect(() => {
    // 创建一个新的定时器，在每一秒钟执行一次函数来更新时间状态。
    const timerIdU = setInterval(() => {
      ss++;
      setCurrentStep(ss);
      if (ss >= 4) {
        clearInterval(timerIdU);
      }
    }, 1000);

    // 清除定时器以防止内存泄漏。
    return () => clearInterval(timerIdU);
  }, []);

  return (
    <div className={styles.container}>
      <h3>Demarcation Result</h3>
      <Steps
        style={{
          padding: '8px 80px'
        }}
        current={currentStep}
        percent={60}
        items={[
          {
            title: 'Quality Statistics',
          },
          {
            title: 'Decision Condition',
          },
          {
            title: "Decision Analysis",
          },
          {
            title: 'Decision Result',
          },
        ]}
      />

      <Row>
        <Col span={6}>
          {
            currentStep >= 1 ?
              <div className={styles.card}>
                <h3>Quality Statistics</h3>
                <Row style={{ marginTop: '70px' }}>
                  <Col span={12}>
                    <div><BorderOuterOutlined className={styles.iconFontSize} /></div>
                    <div style={{ marginBottom: '4px' }}>Teminal</div>
                    <div>0</div>
                  </Col>

                  <Col span={12}>
                    <div><ControlOutlined className={styles.iconFontSize} /></div>
                    <div style={{ marginBottom: '4px' }}>Bearer</div>
                    <div>0</div>
                  </Col>
                </Row>
                <Row style={{ marginTop: '24px' }}>
                  <Col span={12}>
                    <div><ExceptionOutlined className={styles.iconFontSize} /></div>
                    <div style={{ marginBottom: '4px' }}>SP</div>
                    <div>0</div>
                  </Col>

                  <Col span={12}>
                    <div><CloudOutlined className={styles.iconFontSize} /></div>
                    <div style={{ marginBottom: '4px' }}>Resource Pool</div>
                    <div className={restore ? styles.black : styles.red}>1</div>
                  </Col>
                </Row>
              </div>
              :
              null
          }
        </Col>
        <Col span={6}>
          {
            currentStep >= 2 ?
              <div className={styles.card}>
                <h3>Decision Condition</h3>
                <MainGraph restore={restore} />
              </div>
              : null
          }

        </Col>
        <Col span={6}>
          {
            currentStep >= 3 ?
              <div className={styles.card}>
                <h3>host: 172.17.1.3 currentTime: {currentTime.toLocaleString()}</h3>
                <div style={{ marginTop: '30px' }}>
                  <div>CPU: <Slider value={restore ? 45 : 92} styles={{ track: restore ? { background: 'green' } : { background: 'red' } }} /> </div>
                  Hard disk: <Slider defaultValue={49} styles={{ track: { background: 'green' } }} />
                  Memort: <Slider defaultValue={49} styles={{ track: { background: 'green' } }} />
                </div>
              </div>
              :
              null
          }

        </Col>
        <Col span={6}>
          {
            currentStep >= 4 ?
              <div className={styles.card}>
                <h3>Decision Result</h3>
                <div style={{ marginTop: '30px' }}>
                  {
                    restore ?
                      <div>
                        <p>everything is OK.</p>
                      </div>
                      :
                      <div style={{ textAlign: 'left' }}>
                        <h4>Root cause analysis : </h4>
                        <p className={restore ? styles.black : styles.red}>Too high load on the CPU node （172.17.1.3） {"->"} task queue accumulation</p>
                        <h4>Impact scope : </h4>
                        <p>Image processing module （80% task delay）</p>
                      </div>
                  }
                </div>
              </div>
              :
              null
          }

        </Col>
      </Row>

      <Overview getFaultState={getFaultState} />

    </div>
  );
};

export default Fault;