import React, { useState, useEffect } from 'react';
import { Table, Slider, Modal, Descriptions, Button, message } from 'antd';
import styles from './index.less';
import { Scenario, SCENARIO_STORAGE_KEY } from '@/contexts/constants';


const Overview = ({ getFaultState }) => {

    const [current3Time, setCurrent3Time] = useState();
    const [current2Time, setCurrent2Time] = useState();
    const [current1Time, setCurrent1Time] = useState();

    useEffect(() => {
        const now = new Date();
        now.setSeconds(now.getSeconds() - 30);
        setCurrent3Time(now)
        const now1 = new Date();
        now1.setSeconds(now1.getSeconds() - 20);
        setCurrent2Time(now1)
        const now2 = new Date();
        now2.setSeconds(now2.getSeconds() - 10);
        setCurrent1Time(now2)
    }, []);

    const [isModalOpen, setIsModalOpen] = useState(false);

    useEffect(() => {
        // 创建一个新的定时器，在每一秒钟执行一次函数来更新时间状态。
        const timerId = setTimeout(() => {
            setIsModalOpen(true);
        }, 5000);

        // 清除定时器以防止内存泄漏。
        return () => clearTimeout(timerId);
    }, []);

    const handleCancel = () => {
        setIsModalOpen(false);
    };

    // const channel = new BroadcastChannel('tab-communication');

    const restart = () => {
        message.info('Operation successful, restarting in progress');
        setIsModalOpen(false);
        getFaultState('1');
        // 在即将关闭的标签页中
        window.addEventListener('beforeunload', () => {
            localStorage.setItem(SCENARIO_STORAGE_KEY, Scenario.bssMetricRecovered.toString());
            // channel.postMessage({ type: SCENARIO_STORAGE_KEY, data: Scenario.bssMetricRecovered.toString() });
        });
        // setTimeout(() => {
        //     window.close();
        // }, 4000); // 4000毫秒等于3秒
    };

    const borderedItems = [
        {
            key: '1',
            label: 'Fault Description',
            children: (
                <>
                    <span style={{ color: 'red' }}>Controller host 172.17.1.3 is in alarm status (cpu utilization of host hypervisor too high) because some processes consume high CPU</span>
                </>
            ),
            span: "filled"
        },
        {
            key: '2',
            label: 'Root Cause Device',
            children: 'host-172.17.1.3',
            span: "filled"
        },
        {
            key: '3',
            label: 'Suggestion',
            children: (
                <>
                    <b style={{ marginRight: '5px' }}>Restart this process can fix this issue. </b><Button size='small' onClick={restart} type='primary'>accept</Button>
                </>
            ),
            span: "filled"
        },
        {
            key: '4',
            label: 'Root Cause Device Type',
            children: 'Controller',
            span: { xl: 2, xxl: 2 },
        },
        {
            key: '5',
            label: 'Diagnosis Result',
            children: 'Faulty',
            span: { xl: 2, xxl: 2 },
        },
        {
            key: '6',
            label: 'Fault Raised Time',
            span: { xl: 2, xxl: 2 },
            children: (
                <>
                    {current3Time?.toLocaleString()}
                </>
            ),
        },
        {
            key: '7',
            label: 'Fault Analysis Time',
            span: { xl: 2, xxl: 2 },
            children: (
                <>
                    {current1Time?.toLocaleString()}
                </>
            ),
        },
    ];

    const items = [
        {
            key: '1',
            label: 'Collection Object',
            children: 'host-172.17.1.3',
        },
        {
            key: '2',
            label: 'Collection Result',
            children: 'Success',
        },
        {
            key: '3',
            label: 'Collection Time',
            children: (
                <>
                    {current2Time?.toLocaleString()}
                </>
            ),
        },
    ];

    const columns = [
        {
            title: 'No.',
            dataIndex: 'No.',
            key: 'No.',
        },
        {
            title: 'PID',
            dataIndex: 'PID',
            key: 'PID',
        },
        {
            title: 'Process Status',
            dataIndex: 'Process',
            key: 'Process',
        },
        {
            title: 'CPU Usage(%)',
            dataIndex: 'CPU',
            key: 'CPU',
            render: (text, recoord) => {
                if (text === '135') {
                    return <div style={{ color: '#ff0000' }}>{text}</div>
                } else {
                    return <div>{text}</div>
                }
            },
        },
        {
            title: 'CMD',
            dataIndex: 'CMD',
            key: 'CMD',
        },
        {
            title: 'PSR',
            dataIndex: 'PSR',
            key: 'PSR',
        },
        {
            title: 'PPID',
            dataIndex: 'PPID',
            key: 'PPID',
        },
        {
            title: 'User',
            dataIndex: 'User',
            key: 'User',
        },
    ]

    const data = [
        {
            key: '1',
            "No.": '1',
            PID: 627315,
            Process: 'S',
            CPU: '135',
            CMD: '/usr/bin/python3.6/opt/tec',
            PSR: 14,
            PPID: 62697,
            User: "itool"
        },
        {
            key: '2',
            "No.": '2',
            PID: 327016,
            Process: 'S',
            CPU: '12.6',
            CMD: 'sh /usr/bin/time-log 30 50',
            PSR: 9,
            PPID: 4481,
            User: "root"
        },
        {
            key: '3',
            "No.": '3',
            PID: 12635,
            Process: 'S',
            CPU: '11.4',
            CMD: './wsssr_defence_service -c',
            PSR: 18,
            PPID: 1,
            User: "root"
        },
        {
            key: '4',
            "No.": '4',
            PID: 4522,
            Process: 'S',
            CPU: '7.5',
            CMD: '/usr/libexec/platform-pythp',
            PSR: 11,
            PPID: 1,
            User: "root"
        },
        {
            key: '5',
            "No.": '5',
            PID: 627639,
            Process: 'S',
            CPU: '7',
            CMD: '/usr/bin/systemd/systemd --',
            PSR: 23,
            PPID: 1,
            User: "itool"
        },
        {
            key: '6',
            "No.": '6',
            PID: 627316,
            Process: 'S',
            CPU: '25',
            CMD: '/usr/bin/python/opt/tec',
            PSR: 14,
            PPID: 62693,
            User: "itool"
        }
    ]

    const columns1 = [
        {
            title: 'Alarm Code Name',
            dataIndex: 'alarm',
            key: 'alarm',
        },
        {
            title: 'Object Type',
            dataIndex: 'object',
            key: 'object',
        },
        {
            title: 'Object Name',
            dataIndex: 'Name',
            key: 'Name',
        },
        {
            title: 'Occurrence Time',
            dataIndex: 'Occurrence',
            key: 'Occurrence',
        },
        {
            title: 'Clear Time',
            dataIndex: 'Clear',
            key: 'Clear',
        },
        {
            title: 'Additional Information',
            dataIndex: 'Additional',
            key: 'Additional',
        },
    ]

    const data1 = [
        {
            key: '6xx',
            alarm: 'CPU Utilization of Host Hypervisor too High',
            "Object": "Controller",
            "Name": "host-172.17.1.3",
            Occurrence: current3Time?.toLocaleString(),
            Clear: "--",
            Additional: 'cloudname=tecs_public_test01,hid=721560600092'
        }
    ]

    // 分页配置
    const paginationConfig = {
        pageSize: 3, // 每页显示的数据条目数量
        showSizeChanger: false, // 是否允许改变每页显示的数量，默认不允许
        total: data.length, // 数据总数
    };

    return (
        <Modal
            title="Diagnosis List"
            open={isModalOpen}
            footer={null}
            // onCancel={handleCancel}
            width={"80%"}
            closable={false}
            maskClosable={false}
        // styles={{
        //     body: {
        //         height: "calc(100vh)", // 计算后的高度减去header和footer的高度
        //         // overflowY: "scroll",
        //     }
        // }}
        >
            <div>

                <h2 style={{ textAlign: 'center' }}>Controller host 172.17.1.3 is in alarm status (cpu utilization of host hypervisor too high)</h2>
                <div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '8px' }}>
                        <div style={{
                            width: '10px',
                            height: '2em',
                            backgroundColor: '#faad14',
                            marginRight: '8px',
                            marginTop: '-6px'
                        }} />
                        <h3>Fault Overview</h3>
                    </div>

                    <Descriptions
                        bordered
                        title=""
                        size={'small'}
                        items={borderedItems}
                    />
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginTop: '8px' }}>
                    <div style={{
                        width: '10px',
                        height: '2em',
                        backgroundColor: '#faad14',
                        marginRight: '8px',
                        marginTop: '-6px'
                    }} />
                    <h3>Root Cause Details (some processes consume high CPU)</h3>
                </div>
                <div className={styles.container} style={{ height: '280px', color: 'rgba(0,0,0,0.45) !important' }}>
                    <strong>Collect Processer Information With Large Amuount od CPU Usage</strong>
                    <Descriptions items={items} style={{ marginBottom: '8px' }} />
                    <Table columns={columns} dataSource={data} bordered size='small' pagination={paginationConfig} />
                </div>
                <div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '8px' }}>
                        <div style={{
                            width: '10px',
                            height: '2em',
                            backgroundColor: '#faad14',
                            marginRight: '8px',
                            marginTop: '-6px'
                        }} />
                        <h3>Alarms Trigger This Diagnosis</h3>
                    </div>
                    <Table columns={columns1} dataSource={data1} bordered size='small' pagination={false} />
                </div>

            </div>
        </Modal>

    );
};

export default Overview;