import React, { useState, useEffect, useRef } from 'react'
import { getGraphData } from './getGraphDataFunc';

export default function ({ graph, restore }) {

    // 创建对container11 DOM元素的引用
    const containerRef = useRef(null);

    useEffect(() => {

        if (!containerRef.current) return;

        // 获取container11的实际宽度和高度
        const rect = containerRef.current.getBoundingClientRect();

        let grData = getGraphData(restore, rect.width, rect.height);
        try {
            graph && graph.data(grData);
            graph && graph.render();
        } catch (error) {
            console.error(error)
        }

        // 如果需要的话，可以根据获取到的宽度和高度调整graph的配置
        if (graph) {
            graph.changeSize(rect.width, rect.height);
        }
    }, [graph, restore])

    return (
        <div ref={containerRef} id="container11" className="x6-graph" />
    )
}
