
export const getGraphData = (restore, containerWidth, containerHeight) => {

    // 动态计算节点的大小比例
    const baseNodeWidth = 100;
    const baseNodeHeight = 50;

    // 这里可以根据实际情况调整缩放因子
    const scaleFactor = Math.min(containerWidth / 400, containerHeight / 300);
    const dynamicNodeWidth = baseNodeWidth * scaleFactor;
    const dynamicNodeHeight = baseNodeHeight * scaleFactor;

    const col = restore ? 'block' : 'red';
    const fill = restore ? 'transparent' : 'red';

    // 节点
    let nodes = [
        {
            id: '1',       // 唯一标识符ID
            x: 100 * scaleFactor,                  // X坐标值
            y: 50 * scaleFactor,                  // Y坐标值
            label: 'Teminal',         // 显示的文字标签
            type: 'ellipse',          // 形状类型为圆形
            size: [dynamicNodeWidth, dynamicNodeHeight],               // 圆形的直径
            style: {                 // 自定义样式
                fill: 'transparent', // 内部填充颜色为透明
                stroke: 'black'       // 边框颜色为黑色
            },
            labelCfg: {             // 配置文字标签的相关参数
                position: 'center',  // 文字居中显示
                style: {
                    fontSize: 12,      // 字体大小
                    textAlign: 'center',// 对齐方式
                    textBaseline: 'middle' // 基准线对齐方式
                }
            }
        },
        {
            id: '2',       // 唯一标识符ID
            x: 100 * scaleFactor,                  // X坐标值
            y: 100 * scaleFactor,                  // Y坐标值
            label: 'Bearer',         // 显示的文字标签
            type: 'ellipse',          // 形状类型为圆形
            size: [dynamicNodeWidth, dynamicNodeHeight],                 // 圆形的直径
            style: {                 // 自定义样式
                fill: 'transparent', // 内部填充颜色为透明
                stroke: 'black'       // 边框颜色为黑色
            },
            labelCfg: {             // 配置文字标签的相关参数
                position: 'center',  // 文字居中显示
                style: {
                    fontSize: 12,      // 字体大小
                    textAlign: 'center',// 对齐方式
                    textBaseline: 'middle' // 基准线对齐方式
                }
            }
        },
        {
            id: '3',       // 唯一标识符ID
            x: 100 * scaleFactor,                  // X坐标值
            y: 150 * scaleFactor,                  // Y坐标值
            label: 'SP',         // 显示的文字标签
            type: 'ellipse',          // 形状类型为圆形
            size: [dynamicNodeWidth, dynamicNodeHeight],                 // 圆形的直径
            style: {                 // 自定义样式
                fill: 'transparent', // 内部填充颜色为透明
                stroke: 'black'       // 边框颜色为黑色
            },
            labelCfg: {             // 配置文字标签的相关参数
                position: 'center',  // 文字居中显示
                style: {
                    fontSize: 12,      // 字体大小
                    textAlign: 'center',// 对齐方式
                    textBaseline: 'middle' // 基准线对齐方式
                }
            }
        },
        {
            id: '4',       // 唯一标识符ID
            x: 100 * scaleFactor,                  // X坐标值
            y: 200 * scaleFactor,                  // Y坐标值
            label: 'Resource',         // 显示的文字标签
            type: 'ellipse',          // 形状类型为圆形
            size: [dynamicNodeWidth, dynamicNodeHeight],                  // 圆形的直径
            style: {                 // 自定义样式
                fill: 'transparent', // 内部填充颜色为透明
                stroke: col      // 边框颜色为黑色
            },
            labelCfg: {             // 配置文字标签的相关参数
                position: 'center',  // 文字居中显示
                style: {
                    fontSize: 12,      // 字体大小
                    textAlign: 'center',// 对齐方式
                    textBaseline: 'middle' // 基准线对齐方式
                }
            }
        },

        {
            id: '5',       // 唯一标识符ID
            x: 250 * scaleFactor,                  // X坐标值
            y: 100 * scaleFactor,                  // Y坐标值
            label: 'Resource Load',         // 显示的文字标签
            type: 'ellipse',          // 形状类型为圆形
            size: [dynamicNodeWidth, dynamicNodeHeight],                 // 圆形的直径
            style: {                 // 自定义样式
                fill: 'transparent', // 内部填充颜色为透明
                stroke: col,       // 边框颜色为黑色
                lineDash: [5, 5]
            },
            labelCfg: {             // 配置文字标签的相关参数
                position: 'center',  // 文字居中显示
                style: {
                    fontSize: 12,      // 字体大小
                    textAlign: 'center',// 对齐方式
                    textBaseline: 'middle' // 基准线对齐方式
                }
            }
        },
        {
            id: '6',       // 唯一标识符ID
            x: 250 * scaleFactor,                  // X坐标值
            y: 200 * scaleFactor,                  // Y坐标值
            label: 'Fault',         // 显示的文字标签
            type: 'ellipse',          // 形状类型为圆形
            size: [dynamicNodeWidth, dynamicNodeHeight],                 // 圆形的直径
            style: {                 // 自定义样式
                fill: fill, // 内部填充颜色为透明
                stroke: col,       // 边框颜色为黑色
                lineDash: [5, 5]
            },
            labelCfg: {             // 配置文字标签的相关参数
                position: 'center',  // 文字居中显示
                style: {
                    fontSize: 12,      // 字体大小
                    textAlign: 'center',// 对齐方式
                    textBaseline: 'middle' // 基准线对齐方式
                }
            }
        },
        {
            id: '7',       // 唯一标识符ID
            x: 250 * scaleFactor,                  // X坐标值
            y: 300 * scaleFactor,                  // Y坐标值
            label: 'Network Performance',         // 显示的文字标签
            type: 'ellipse',          // 形状类型为圆形
            size: [dynamicNodeWidth, dynamicNodeHeight],                // 圆形的直径
            style: {                 // 自定义样式
                fill: 'transparent', // 内部填充颜色为透明
                stroke: col,       // 边框颜色为黑色
                lineDash: [5, 5]
            },
            labelCfg: {             // 配置文字标签的相关参数
                position: 'center',  // 文字居中显示
                style: {
                    fontSize: 12,      // 字体大小
                    textAlign: 'center',// 对齐方式
                    textBaseline: 'middle' // 基准线对齐方式
                }
            }
        }
    ];

    // 边
    const edges = [
        {
            source: '4',
            target: '5',
            // type: 'cubic-horizontal',
            style: {
                stroke: col,
                // lineWidth: 2,
                endArrow: true, // 添加箭头指向目标节点
            }
        },
        {
            source: '4',
            target: '6',
            // type: 'cubic-horizontal',
            style: {
                stroke: col,
                // lineWidth: 2,
                endArrow: true, // 添加箭头指向目标节点
            }
        },
        {
            source: '4',
            target: '7',
            // type: 'cubic-horizontal',
            style: {
                stroke: col,
                // lineWidth: 2,
                endArrow: true, // 添加箭头指向目标节点
            }
        },
    ];

    const res = {
        nodes: nodes,
        edges: edges,
    }

    return res;
}